.new-collection{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 150vh;
    margin-bottom: 100px;
    
}
.new-collection h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
}


.new-collection{
margin-top: 50px;
display: flex;
gap: 30px;
}    
.collection{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 30px;

}